@import url('https://fonts.googleapis.com/css2?family=Inter:wght@100..900&display=swap');
@font-face {
    font-family: 'SFProDisplayBold';
    src: url('../assets/font/SF-Pro-Display-Bold.otf') format('opentype');
    font-weight: 700;
    font-style: normal;
}

@font-face {
    font-family: 'SFProDisplayMedium';
    src: url('../assets/font/SF-Pro-Display-Medium.otf') format('opentype');
    font-weight: 500;
    font-style: normal;
}

@font-face {
    font-family: 'SFProDisplayRegular';
    src: url('../assets/font/SF-Pro-Display-Regular.otf') format('opentype');
    font-weight: 400;
    font-style: normal;
}

@font-face {
    font-family: 'SFProDisplayLight';
    src: url('../assets/font/SF-Pro-Display-Light.otf') format('opentype');
    font-weight: 300;
    font-style: normal;
}

// Dodaj więcej @font-face dla innych wariantów fontu...
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    font-family: 'Inter', sans-serif;
    font-family: 'SFProDisplayRegular', sans-serif;
    background-color: #141414;
    background-color: #fff;
    color: #fff;
    text-align: center;
    cursor: none;
    overflow-x: hidden;
}

h1 {
    color: #fff;
    font-size: 3rem;
}

button {
    background-color: #007bff;
    font-size: 1.25rem;
    color: #fff;
    border: none;
    padding: 10px 20px;
    cursor: pointer;
    margin-top: 10px;
    border-radius: 5px;
    transition: .2s;
    &:hover {
        background-color: #0056b3;
    }
}

.container {
    display: flex;
    gap: 20px;
}

.image-wrapper {
    position: relative;
    img {
        display: block;
        display: noscale8,
    }
}

.cursor {
    position: fixed;
    pointer-events: none;
    z-index: 1000;
    transition: opacity 0.3s ease, transform 0.3s ease;
}

.cursor__dot {
    width: 18px;
    height: 18px;
    border-radius: 50%;
    background-color: #e92b2b;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    opacity: 1;
    transition: width 0.3s ease, height 0.3s ease;
}

.cursor__text {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%) scale(0, 0);
    text-align: center;
    font-size: 16px;
    text-transform: uppercase;
    color: #fff;
    opacity: 0;
    transition: opacity 0.3s ease, transform 0.3s ease;
}

.cursor.large .cursor__dot {
    width: 72.5px;
    height: 72.5px;
}

.cursor.large .cursor__text {
    opacity: 1;
    transform: translate(-50%, -50%) scale(1, 1);
}

.buttons-wrapper {
    width: 100%;
    margin: 10vh auto;
    padding: 17.5vh 20%;
    background-color: rgb(225, 225, 225);
    display: flex;
    justify-content: space-between;
    align-items: center;
    .button {
        font-family: 'Inter', sans-serif;
        color: #000;
        background-color: #fff;
        text-transform: uppercase;
        border-radius: 100px;
        position: relative;
        overflow: hidden;
        cursor: pointer;
        padding: 20px 35px;
    }
    .button-1 {
        display: flex;
        align-items: center;
        gap: 15px;
        .rounded {
            width: 8px;
            height: 8px;
            border-radius: 50%;
            background-color: #000;
            transition: all .3s ease-out;
        }
        .primary {
            margin: 0;
            font-size: 16px;
            transition: all .3s ease-out;
            z-index: 2;
        }
        .secondary {
            position: absolute;
            left: 100%;
            transition: transform .3s ease-out;
            z-index: 2;
            color: #fff;
            /*top: 22.5px;*/
        }
        &:hover {
            .rounded {
                transform: scale(40);
                background-color: #0a00c1;
            }
            .primary {
                color: #fff;
                transform: translateX(-25px);
            }
            .secondary {
                transform: translateX(-50px);
            }
        }
    }
    .button-2 {
        .primary {
            top: 0;
            position: relative;
            transition: top .4s cubic-bezier(.33, 1, .68, 1);
        }
        .secondary {
            position: absolute;
            top: 110%;
            left: 0;
            width: 100%;
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;
            transition: top .4s cubic-bezier(.33, 1, .68, 1);
            p {
                position: absolute;
            }
            div {
                background-color: #c9ff18;
                width: 60%;
                height: 100%;
                border-radius: 50%;
                transition: all .4s cubic-bezier(.33, 1, .68, 1);
            }
        }
        &:hover {
            .primary {
                top: -40px;
            }
            .secondary {
                top: 0;
                div {
                    width: 100%;
                    border-radius: 100px;
                }
            }
        }
    }
    .button-3 {
        padding: 20px 0;
        .mask {
            overflow: hidden;
            position: relative;
            .slider {
                display: flex;
                position: relative;
                animation: text_slide 3.75s linear 0s infinite;
                transition: top .5s ease-out;
                top: 0;
                p {
                    margin: 0;
                    text-transform: uppercase;
                    white-space: nowrap;
                    &:nth-of-type(2) {
                        position: absolute;
                        left: calc(100% + 15px);
                    }
                }
            }
            .hover-text {
                position: absolute;
                top: 0;
                left: 50%;
                transform: translateX(-50%);
                span {
                    position: relative;
                    color: transparent;
                    top: 18px;
                    transition: top .25s ease-out;
                }
            }
        }
        &:hover {
            background-color: #000;
            .slider {
                transition: top .75s ease-out;
                top: -18px;
            }
            .hover-text span {
                color: #fff;
                top: 0;
            }
        }
    }
}

@keyframes text_slide {
    100% {
        transform: translateX(calc(-100% - 15px));
    }
}

.intro-container {
    height: 125vh;
    overflow: hidden;
    img {
        object-fit: cover;
        height: 100%;
        width: 100%;
    }
}

.description-wrapper {
    background-color: #141414 !important;
    padding-top: 10rem;
    padding-bottom: 10rem;
    display: flex;
    justify-content: center;
    .description-heading {
        max-width: 57.5vw;
        font-size: 7.5vw;
        font-weight: 500;
        line-height: 1;
        text-align: center;
        text-wrap: balance;
        //user-select: none;
    }
}

.parallax-section {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    width: 100%;
    overflow: hidden;
    .parallax-section_text {
        position: relative;
        z-index: 10;
        padding: 3rem 1rem;
        color: #fff;
        width: 100%;
        height: 100%;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        align-items: center;
        font-weight: 500;
        mix-blend-mode: soft-light;
        p {
            width: 60vw;
            font-size: 2.5vw;
            user-select: none;
        }
        p:nth-of-type(2) {
            width: auto;
            font-size: 5.25vw;
            color: black;
        }
    }
    .img-container {
        position: fixed;
        width: 100%;
        top: -10vh;
        left: 0;
        height: 120vh;
        .img-wrapper {
            position: relative;
            width: 100%;
            img {
                object-fit: cover;
                height: 100%;
                width: 100%;
                filter: grayscale(100%) brightness(75%);
            }
        }
    }
}

// obrazek po srodku na hover sie zwieksza a po bokach znikaja - paralax
.zoom-parallax-container {
    margin: 15vh 0 90vh;
    height: 300vh;
    position: relative;
    background-color: #141414;
    .sticky-div {
        position: sticky;
        top: 0px;
        height: 100vh;
        overflow: hidden;
        .wrapper {
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            display: flex;
            align-items: center;
            justify-content: center;
            .img-container {
                width: 25vw;
                height: 25vh;
                position: relative;
                .name-wrapper {
                    position: absolute;
                    top: 50%;
                    left: 50%;
                    transform: translate(-50%, -50%);
                    mix-blend-mode: difference;
                    .brand-name {
                        font-size: 3rem;
                        font-weight: 600;
                        color: #fff;
                        transform-origin: center;
                    }
                }
                img {
                    height: 100%;
                    width: 100%;
                    object-fit: cover;
                }
            }
            &:nth-of-type(2) {
                .img-container {
                    top: -32.5vh;
                    left: 3.75vw;
                    width: 32.5vw;
                    height: 30vh;
                }
            }
            &:nth-of-type(3) {
                .img-container {
                    top: -15vh;
                    left: -25vw;
                    width: 20vw;
                    height: 55vh;
                }
            }
            &:nth-of-type(4) {
                .img-container {
                    left: 27.5vw;
                    width: 25vw;
                    height: 25vh;
                }
            }
            &:nth-of-type(5) {
                .img-container {
                    top: 30vh;
                    left: 5vw;
                    width: 20vw;
                    height: 27.5vh;
                }
            }
            &:nth-of-type(6) {
                .img-container {
                    top: 30vh;
                    left: -22.5vw;
                    width: 30vw;
                    height: 27.5vh;
                }
            }
            &:nth-of-type(7) {
                .img-container {
                    top: 27.5vh;
                    left: 27.5vw;
                    width: 17.5vw;
                    height: 22.5vh;
                }
            }
            &:nth-of-type(8) {
                .img-container {
                    top: -32.5vh;
                    left: 30vw;
                    width: 15vw;
                    height: 30vh;
                }
            }
        }
    }
}

// stack kard - on hover nakładają sie na siebie
.cards-parallax {
    margin: 50vh 0 30vh;
    .card-container {
        height: 100vh;
        display: flex;
        align-items: center;
        justify-content: center;
        position: sticky;
        top: 0px;
        .card {
            display: flex;
            flex-direction: column;
            position: relative;
            height: 500px;
            width: 1000px;
            height: 55vh;
            width: 60vw;
            border-radius: 25px;
            padding: 40px 50px 50px;
            transform-origin: top;
            h2 {
                text-align: center;
                margin: 0px;
                font-size: 28px;
            }
            .card-Body {
                display: flex;
                height: 100%;
                margin-top: 50px;
                gap: 50px;
                overflow: hidden;
                .description {
                    width: 40%;
                    position: relative;
                    top: 10%;
                    text-align: left;
                    text-wrap: balance;
                    p {
                        font-size: 16px;
                        &::first-letter {
                            font-size: 28px;
                            font-family: 'Title';
                        }
                    }
                    span {
                        display: flex;
                        align-items: center;
                        gap: 5px;
                        a {
                            font-size: 12px;
                            text-decoration: underline;
                            cursor: pointer;
                        }
                    }
                }
                .image-Container {
                    position: relative;
                    width: 60%;
                    height: 100%;
                    border-radius: 20px;
                    overflow: hidden;
                    .inner {
                        width: 100%;
                        height: 100%;
                    }
                    img {
                        width: 100%;
                        height: 100%;
                        object-fit: cover;
                    }
                }
            }
        }
    }
}

// projekty po hoverze rozsuwane na bok i jest obrazek
.slide-projects-gallery {
    margin: 0vh 0 25vh;
    height: 100vh;
    display: flex;
    align-items: center;
    justify-content: center;
    .gallery-container {
        width: 70%;
    }
    .project {
        border-top: 2px solid black;
        padding: 0.8vw 0;
        cursor: pointer;
        width: 100%;
        display: flex;
        justify-content: center;
        align-items: center;
        p {
            color: #000;
            font-size: 5vw;
            font-size: 6rem;
            &:nth-of-type(1) {
                margin-right: 0.75vw;
            }
            &:nth-of-type(2) {
                margin-left: 0.75vw;
            }
        }
        .img-wrapper {
            overflow: hidden;
            display: flex;
            justify-content: center;
            width: 0;
            img {
                width: 11vw;
                object-fit: cover;
                aspect-ratio: 1.3/1;
            }
        }
        &:last-of-type {
            border-bottom: 2px solid black;
        }
    }
}

#projects-gallery {
    display: flex;
    height: 100vh;
    align-items: center;
    justify-content: center;
    .projects-gallery-container {
        width: 75vw;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
    }
    .project {
        color: #000;
        display: flex;
        width: 100%;
        justify-content: space-between;
        align-items: center;
        padding: 50px 75px;
        border-top: 1px solid rgb(201, 201, 201);
        cursor: pointer;
        transition: all 0.2s;
        &:last-of-type {
            border-bottom: 1px solid rgb(201, 201, 201);
        }
        h2 {
            font-size: 60px;
            margin: 0px;
            font-weight: 400;
            transition: all 0.4s;
            //font-family: 'SFProDisplayMedium', sans-serif;
        }
        p {
            transition: all 0.4s;
            font-family: 'SFProDisplayLight', sans-serif;
        }
        &:hover {
            opacity: 0.5;
            h2 {
                transform: translateX(-10px);
            }
            p {
                transform: translateX(10px);
            }
        }
    }
    .modalContainer {
        height: 350px;
        width: 400px;
        height: 35vh;
        width: 22.5vw;
        position: absolute;
        background-color: white;
        overflow: hidden;
        pointer-events: none;
        display: flex;
        align-items: center;
        justify-content: center;
        .modalSlider {
            height: 100%;
            width: 100%;
            position: absolute;
            transition: top 0.5s cubic-bezier(0.76, 0, 0.24, 1);
            .modal {
                height: 100%;
                width: 100%;
                display: flex;
                align-items: center;
                justify-content: center;
                img {
                    object-fit: cover;
                    aspect-ratio: 1.5/1;
                    height: auto;
                }
            }
        }
    }
    .cursor-gallery,
    .cursorLabel {
        font-size: 14px;
        font-weight: 300;
        text-transform: uppercase;
        background-color: #0022ff;
        color: white;
        width: 75px;
        height: 75px;
        border-radius: 50%;
        position: absolute;
        z-index: 2;
        display: flex;
        align-items: center;
        justify-content: center;
        pointer-events: none;
    }
    .cursorLabel {
        background-color: transparent;
    }
}

.animatedPhrase-section {
    font-family: 'SFProDisplayMedium', sans-serif;
    font-family: 'Inter', sans-serif;
    background-color: #141414;
    color: rgb(255, 255, 255);
    display: flex;
    align-items: flex-end;
    justify-content: flex-start;
    height: 100vh;
    padding: 100vh 0;
    .phrase-wrapper {
        margin-left: 7.5%;
        width: 50%;
        display: flex;
        flex-wrap: wrap;
        p {
            font-size: 2.5rem;
            margin: 0px;
            margin-right: 1.25vw;
            font-weight: 700;
            text-wrap: balance;
        }
        span {
            opacity: 0.1;
            margin-right: 3px;
            &::selection {
                background-color: #ffffff;
                color: #000000;
            }
        }
    }
}

@media screen and (max-width: 1100px) {
    body {
        cursor: auto;
    }
    .image-wrapper {
        img {
            display: none;
        }
    }
    .cursor {
        display: none
    }
}